@font-face {
  font-family: 'Cerebri Sans';
  src: url("/github.io/reactmoonex.git/static/media/Cerebri Sans Book.f4cb1154.ttf");
}
@font-face {
  font-family: 'Cerebri Sans Bold';
  src: url("/github.io/reactmoonex.git/static/media/Cerebri Sans Bold.4180e23f.ttf");
}
@font-face {
  font-family: 'Cerebri Sans ExtraBold';
  src: url("/github.io/reactmoonex.git/static/media/Cerebri Sans ExtraBold.562a9e39.ttf");
}
@font-face {
  font-family: 'Cerebri Sans Heavy';
  src: url("/github.io/reactmoonex.git/static/media/Cerebri Sans Heavy.4e5720eb.ttf");
}

:root {
  --primaryColor: #fa4529;
  --primaryBgColor: #fa4529;
  --btnBgColor: #e03d24;
  --titlebarBgColor: #faebd7;
  --headingBold: 'Cerebri Sans Bold';
  --headingExtraBold: 'Cerebri Sans ExtraBold';
  --headingHeavy: 'Cerebri Sans Heavy';
  --textdark: #000;
}

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

body {
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Cerebri Sans', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.5;
  color: #666;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.parent-smoothscroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
::selection {
  color: #fff;
  background: #fa4529;
  background: var(--primaryBgColor);
}

/* custom scrollbar */
::-webkit-scrollbar-track {
	background-color: #F5F5F5;
}
::-webkit-scrollbar {
	width: 8px;
	background-color: #F5F5F5;
}
::-webkit-scrollbar-thumb {
	background-color: #fa4529;
	background-color: var(--primaryBgColor);
}

.h-text-dark {
  color: #000;
  color: var(--textdark);
}
a {
  text-decoration: none;
}
a, button {
  cursor: pointer!important;
}
.h-btn-primary {
  display: inline-block;
  font-size: 18px;
  font-family: 'Cerebri Sans Bold';
  font-family: var(--headingBold);
  background: #e03d24;
  background: var(--btnBgColor);
  color: #fff;
  padding: 23px 45px;
  transition: all .3s ease-in-out;
}
.h-btn-primary:hover {
  color: #fff;
  background: #000;
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
}

h2, h3, h4, h5, h6 {
  font-family: 'Cerebri Sans ExtraBold';
  font-family: var(--headingExtraBold);
  line-height: 1.1;
  margin-top: 30px;
  margin-bottom: 30px;
  color: #000;
}
h1 {
  font-family: 'Cerebri Sans Heavy';
  font-family: var(--headingHeavy);
  font-size: 230px;
  line-height: 1.2;
  letter-spacing: -.04em;
  margin-top: 0;
  margin-bottom: 0;
  color: #000;
}
h2 {
  font-size: 80px;
}
h3 {
  font-size: 60px;
}
h4 {
  font-size: 40px;
}
h5 {
  font-size: 30px;
}
.h-font-md {
  font-size: 70px;
  line-height: 1;
}
.h-bodyFont-md {
  font-size: 24px;
  line-height: 1.5;
}
.h-font-18 {
  font-size: 18px;
  line-height: 1.5;
}

.h-row {
  display: flex;
  flex-wrap: wrap;
  padding: 0px 15px;
}
.h-row>* {
  max-width: 100%;
}

/* columns start */
.h-col-12 {
  flex: 0 0 auto;
  width: 100%;
}
.h-col-10 {
  flex: 0 0 auto;
  width: 85%;
}
.h-col-9 {
  flex: 0 0 auto;
  width: 75%;
}
.h-col-8 {
  flex: 0 0 auto;
  width: 66.66666667%;
}
.h-col-6 {
  flex: 0 0 auto;
  width: 50%;
}
.h-col-4 {
  flex: 0 0 auto;
  width: 33.33333333%;
}
.h-col-3 {
  flex: 0 0 auto;
  width: 25%;
}
.h-col-offset-1 {
  margin-left: 8.33333333%;
}
.h-w-56 {
  width: 56%;
}
/* columns end */

/* Page Loader */
.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  background: #fa4529;
  background: var(--primaryBgColor);
}
.se-pre-con img {
  -webkit-filter: invert(1);
          filter: invert(1);
}

/* spacing start */
.h-pd-top-bottom-xl {
  padding-top: 160px;
  padding-bottom: 160px;
}
.h-pd-top-bottom-l {
  padding-top: 110px;
  padding-bottom: 110px;
}
.h-pd-top-bottom-m {
  padding-top: 60px;
  padding-bottom: 60px;
}
.h-pd-top-bottom-s {
  padding-top: 30px;
  padding-bottom: 30px;
}
.h-pd-top-xl {
  padding-top: 160px;
}
.h-pd-bottom-xl {
  padding-bottom: 160px;
}
.h-pd-top-l {
  padding-top: 110px;
}
.h-pd-bottom-l {
  padding-bottom: 110px;
}
.h-pd-top-m {
  padding-top: 60px;
}
.h-pd-bottom-m {
  padding-bottom: 60px;
}
.h-pd-top-s {
  padding-top: 40px;
}
.h-pd-bottom-s {
  padding-bottom: 40px;
}
.h-mr-top-0 {
  margin-top: 0;
}
.h-mr-bottom-0 {
  margin-bottom: 0;
}
/* spacing end */

.primaryBgColor {
  background: #fa4529;
  background: var(--primaryBgColor);
}
.titlebar-bg {
  background: #faebd7;
  background: var(--titlebarBgColor);
}
.h-text-white {
  color: #fff;
}
.h-text-uppercase {
  text-transform: uppercase;
}
.h-img-w {
  width: 100%;
}

.h-hr-dark {
  border-color: #000;;
}
.h-pos-relative {
  position: relative;
}
.h-hero-bg-img {
  background: url(/github.io/reactmoonex.git/static/media/main-image.a743b2a8.png) no-repeat #fa4529;
  background-position: calc(50vw - 100px) calc(50% + 150px);
}
.spin-circle {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.spin-circle {
  -webkit-animation: circle_spin 10s linear infinite;
  animation: circle_spin 10s linear infinite;
}
@-webkit-keyframes circle_spin {
  100% {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
@keyframes circle_spin {
  100% {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

.vh-100 {
  height: 100vh;
}
.h-d-flex {
  display: flex;
}
.d-flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.d-flex-col {
  flex-direction: column;
}
.gap-15 {
  grid-gap: 15px;
  gap: 15px;
}
.text-center {
  text-align: center;
}

.h-service-box {
  position: relative;
  display: grid;
  grid-template-columns: 7.5rem 18.75rem 1fr 8.75rem;
  padding: 3.125rem;
  border-top: 2px solid #000;
  transition: background-color ease .4s;
}
.h-service-box-link {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.h-service-box-icon {
  color: #fa4529;
  font-size: 3.75rem;
  line-height: 1.1;
}
.h-service-box-title {
  text-transform: uppercase;
  font-size: 1.5rem;
  line-height: 1.5;
  margin: 0;
}
.h-service-box-arrow {
  text-align: right;
  color: #000;
  font-size: 2.25rem;
  transition: all .3s ease-in-out;
  transition: all ease .4s;
}
.h-service-box:hover {
  background-color: #f7f7f7;
}
.h-service-box:hover .h-service-box-arrow {
  color: #fa4529;
  -webkit-transform: translateX(2px);
  transform: translateX(2px);
}

.h-counter-value {
  color: #000;
}
.h-counter-item {
  flex-basis: 25%
}

.h-t-bg {
  background: url(/github.io/reactmoonex.git/static/media/testimonial-bg.017b088b.png) no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: 50%;
}
.t-wrapper .carousel .slide {
  text-align: left;
}
.t-content {
  font-size: 48px;
  line-height: 1.33;
  font-weight: 700;
  color: #fff;
}
.t-author {
  font-size: 24px;
  font-weight: 700;
  color: #fff;
}
.t-designation {
  color: #fff;
  font-size: 14px;
  margin-top: 5px;
}

.h-social-icon {
  padding: 20px 20px 20px 0px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 25px;
  gap: 25px;
}
.h-social-icon a,
.h-icon-style a {
  font-size: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fafafa;
  background: #fa4529;
  background: var(--primaryBgColor);
  border: 1px solid #fafafa;
  transition: all ease .2s;
}
.h-social-icon a:hover,
.h-icon-style a:hover {
  color: #fa4529;
  color: var(--primaryColor);
  background: #fafafa;
  border: 1px solid #fa4529;
  border: 1px solid var(--primaryColor);
}

.h-dark-link {
  color: #fa4529;
  color: var(--primaryColor);
}
.h-border-right {
  border-right: 2px solid #faebd7;
  border-right: 2px solid var(--titlebarBgColor);
}


/* PORTFOLIO */
.filter-wrapper button {
  font-size: 16px;
  padding: 10px 15px;
  margin-right: 2%;
  border: 0px;
  transition: all ease .4s;
}
.btn-filter.active, .btn-filter:hover {
  background: #e03d24;
  background: var(--btnBgColor);
  color: #fff;
}
.portfolio-grid {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 2%;
  gap: 2%;
}
.portfolio-grid .item-filter {
  flex-basis: 32%;
  margin-bottom: 40px;
  display: none;
}
.portfolio-grid .item-filter.show {
  display: block;
}
.portfolio-grid .item-title {
  margin-top: 15px;
}
.portfolio-grid .item-category {
  margin-top: 15px;
}

.portfolio__labels {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}
.portfolio__labels span {
  background: #f4f4f4;
  padding: 5px 15px;
  cursor: pointer;
}
.portfolio__labels span[active='1'] {
  background: #e03d24;
  background: var(--btnBgColor);
  color: #fff;
}
.portfolio-grid .item-filter.show {
  display: block;
  box-shadow: 0 8px 10px #efefef;
  border-radius: 0 0 8px 8px;
}
.portfolio-grid .item-filter .item-content {
  padding: 0 15px;
}

.layout-grid-option {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.layout-grid-option img {
  -webkit-filter: brightness(0);
          filter: brightness(0);
}
.grid-view-three img {
  -webkit-filter: brightness(1);
          filter: brightness(1);
}

.portfolio-grid .item-filter:hover .item-img img,
.portfolio-masonry .item-filter:hover .item-img img {
  -webkit-transform: scale(1.4) rotate(5deg);
          transform: scale(1.4) rotate(5deg);
}
.portfolio-grid .item-filter .item-img,
.portfolio-masonry .item-filter .item-img {
  overflow: hidden;
}
.portfolio-grid .item-filter .item-img img,
.portfolio-masonry .item-filter .item-img img {
  transition: all ease .8s;
}

/* PORTFOLIO MASONRY */
.portfolio-masonry {
  -webkit-columns: 3;
          columns: 3;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
.portfolio-masonry .item-filter {
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
}
.portfolio-masonry .item-filter.show {
  display: block;
}
.portfolio-masonry .item-title {
  margin-top: 15px;
  margin-bottom: 15px;
}
.portfolio-type-2 .item-filter.show {
  display: block;
  box-shadow: 0 8px 10px #efefef;
  border-radius: 0 0 8px 8px;
  margin-bottom: 30px;
}
.portfolio-type-2 .item-filter .item-content {
  padding: 15px;
}

.h-footer-col-1, .h-footer-col-3 {
  flex-basis: 25%;
}
.h-footer-col-2 {
  flex-basis: 50%;
}


/* Media Query */
@media  screen and (min-width: 1439.99px) {
  h1 {
    font-size: 240px;
  }
  h2 {
    font-size: 90px;
  }
  h3 {
    font-size: 70px;
  }
  .h-bodyFont-md {
    font-size: 30px;
  }
  .h-hero-bg-img {
    background-position: calc(52vw - 100px) calc(45% + 150px);
    background-size: 77.5%;
  }
}

@media  screen and (min-width: 1679.99px) {
  .h-hero-bg-img {
    background-position: calc(54vw - 100px) calc(55% + 150px);
    background-size: 75%;
  }
}


@media (max-width: 1280px) {
  h1 {
    font-size: 130px;
  }
  .h-w-56 {
    width: 70%;
  }
}

@media (max-width: 999.98px) {
  .h-col-3, .h-col-4, .h-col-6, .h-col-8, .h-col-9, .h-col-10 {
    width: 100%;
  }
  .h-col-offset-1 {
    margin-left: 0%;
  }
  .h-hide-xl {
    display: none;
  }
  .h-w-56 {
    width: 96%;
  }
  .h-pd-top-xl {
    padding-top: 80px;
  }
  .h-pd-bottom-xl {
    padding-bottom: 80px;
  }
  .h-pd-bottom-l {
    padding-bottom: 60px;
  }
  .h-pd-bottom-s {
    padding-bottom: 20px;
  }
  h2 {
    font-size: 60px;
  }
  h3 {
    font-size: 50px;
  }
  .t-content {
    font-size: 32px;
  }
  .h-service-box {
    grid-template-columns: 5.5rem 18.75rem 1fr 4.75rem;
  }
  .portfolio-grid .item-filter {
    flex-basis: 49%;
  }
  .layout-grid-option {
    justify-content: flex-start;
    margin-top: 20px;
  }
  .layout-grid-option img {
    width: 20px;
    height: 20px;
  }
  .h-footer .d-flex-center {
    justify-content: space-between;
    align-items: flex-start;
  }
  .h-footer-col-2 {
    flex-basis: 30%;
  }
  .h-social-icon {
    padding: 0px 20px 20px 0px;
  }
  .h-footer-logo {
    padding-bottom: 20px;
  }
  .h-footer .text-center {
    text-align: left;
  }
  .h-footer .h-social-icon {
    justify-content: flex-start;
  }
  .h-border-right {
    border-right: 0;
    border-bottom: 2px solid #faebd7;
    border-bottom: 2px solid var(--titlebarBgColor);
  }
  .h-mob-pd-bottom {
    padding-bottom: 30px;
  }
  .h-mob-pd-top {
    padding-top: 30px;
  }
}

@media (max-width: 767.98px) {
  .h-w-56 {
    width: 100%;
  }
  h2, h3, h4, h5, h6 {
    margin-top: 15px;
    margin-bottom: 15px;
  }
  body {
    font-size: 15px;
  }
  h1 {
    font-size: 78px;
  }
  h2 {
    font-size: 50px;
  }
  h3 {
    font-size: 40px;
  }
  h4 {
    font-size: 30px;
  }
  h5 {
    font-size: 20px;
  }
  .h-font-md {
    font-size: 35px;
    line-height: 1.2;
  }
  .h-bodyFont-md {
    font-size: 21px;
  }
  .h-font-18 {
    font-size: 16px;
  }
  .h-pd-top-l {
    padding-top: 60px;
  }
  .h-pd-top-bottom-l {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .h-pd-top-bottom-m {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .h-btn-primary {
    font-size: 15px;
    padding: 18px 38px;
  }
  .h-service-box {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
  }
  .h-service-box-arrow {
    display: none;
  }
}

@media (max-width: 575.98px) {
  .h-font-md {
    font-size: 32px;
  }
  h4 {
    font-size: 24px;
  }
  h5 {
    font-size: 16px;
  }
  .h-hero-bg-img {
    background-position: calc(50vw - 100px) calc(90% + 150px);
  }
  .t-content {
    font-size: 24px;
  }
  .h-counter-item {
    flex-basis: 50%;
    padding-bottom: 40px;
  }
  .layout-grid-option div:last-child {
    display: none;
  }
  .h-footer-col-1, .h-footer-col-2, .h-footer-col-3 {
    flex-basis: 100%;
  }
  .h-footer .h-social-icon {
    padding-top: 20px;
  }
}
header {
    background: #e03d24;
    padding: 0px 15px;
    height: 90px;
}
.header-wrapper {
    display: flex;
    align-items: center;
    height: 100%;
}
#logo, #hemburger_menu {
    flex-basis: 20%;
}
#logo a {
    display: block;
}
#menu {
    flex-basis: 60%;
}
#menu, #menu nav, #menu .main-menu {
    height: 100%;
}
#menu ul.main-menu {
    display: flex;
    grid-gap: 50px;
    gap: 50px;
}
#menu ul li {
    list-style: none;
    position: relative;
}
#menu ul.main-menu li {
    display: flex;
    align-items: center;
}
#menu ul li a {
    font-family: 'Cerebri Sans Bold';
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    transition: color linear .2s;
    position: relative;
}
#menu ul > li > a {
    display: flex;
    align-items: center;
    height: 100%;
}
#menu ul li a.active,
#menu ul.main-menu > li > a:hover,
#menu ul.main-menu > li:hover > a {
    color: #fafafa;
}
#menu ul.main-menu li.has-children:hover:after {
    content: "";
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid #fff;
    position: absolute;
    bottom: -6px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
#menu ul li .sub-menu {
    position: absolute;
    top: 100%;
    background: #fff;
    padding: 8px 15px;
    min-width: 200px;
    display: none;
}
#menu ul li .sub-menu a.active {
    color: #e03d24;
}
#menu ul li.has-children:hover .sub-menu {
    display: block;
}
#menu .sub-menu li:first-child {
    padding-top: 10px;
}
#menu .sub-menu li {
    padding: 5px;
}
#menu .main-menu .sub-menu li a:hover {
    color: #e03d24;
}

#hemburger_menu .header-right-wrapper {
    display: flex;
    justify-content: flex-end;
    grid-gap: 25px;
    gap: 25px;
}
.search-icon i, .hemburger-icon i {
    font-size: 28px;
    cursor: pointer;
    color: #000;
}
.close-icon {
    text-align: right;
    padding-right: 15px;
}
.close-icon i {
    font-size: 40px;
    cursor: pointer;
}

#mobile_menu {
    background: #fafafa;
    position: fixed;
    top: 0;
    right: 0;
    width: 0vw;
    height: 100vh;
    padding-top: 20px;
    padding-bottom: 40px;
    transition: all linear .4s;
}
#mobile_menu.show {
    width: 35vw;
    z-index: 9999;
}
.mobile-menu-wrapper {
    overflow-y: auto;
    height: 100%;
    margin-right: 40px;
}
#mobile_menu ul {
    padding-left: 40px;
}
#mobile_menu ul li {
    list-style: none;
    padding: 8px 35px 8px 20px;
    position: relative;
}
#mobile_menu ul li .sub-menu li {
    padding: 8px 0px 8px 20px;
}
#mobile_menu ul li .sub-menu li:after {
    content: "-";
    position: absolute;
    left: 0;
}
#mobile_menu ul li.has-children .dropdown {
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #000;
    position: absolute;
    top: 30px;
    right: 35px;
    cursor: pointer;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: all ease .4s;
}
#mobile_menu ul li.has-children .dropdown.show {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
#mobile_menu ul li.has-children .sub-menu {
    display: none;
}
