#mobile_menu ul li a {
    font-family: 'Cerebri Sans Bold';
    font-size: 30px;
    line-height: 1.8;
    color: #000;
    text-transform: uppercase;
    transition: color linear .4s;
    position: relative;
}
#mobile_menu ul li .sub-menu li a {
    font-size: 20px;
    line-height: 1.5;
}
#mobile_menu ul li a.active, #mobile_menu ul li a:hover {
    color: #e03d24;
}

#mobile_menu_social {
    padding: 20px 20px 20px 60px;
    display: flex;
    align-items: center;
    grid-gap: 25px;
    gap: 25px;
}
#mobile_menu_social a {
    font-size: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fafafa;
    background: #e03d24;
    border: 1px solid #fafafa;
    transition: all ease .2s;
}
#mobile_menu_social a:hover {
    color: #e03d24;
    background: #fafafa;
    border: 1px solid #e03d24;
}


@media  screen and (min-width: 1439.99px) {
    #menu ul li a {
        font-size: 20px;
    }
}
@media (max-width: 1000px) {
    #logo, #hemburger_menu {
        flex-basis: 50%;
    }
    #mobile_menu.show {
        width: 70vw;
    }
}
@media (max-width: 575.98px) {
    #mobile_menu.show {
        width: 96vw;
    }
    #mobile_menu ul {
        padding-left: 20px;
    }
    #mobile_menu ul li a {
        font-size: 20px;
        line-height: 1.4;
    }
    #mobile_menu ul li .sub-menu li a {
        font-size: 16px;
        line-height: 1.2;
    }
    #mobile_menu .mobile-menu-wrapper ul li.has-children .sub-menu:before {
        content: "";
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 8px solid #000;
        top: 16px;
    }
    #mobile_menu_social {
        padding: 20px 20px 20px 40px;
    }
    #mobile_menu_social a {
        font-size: 16px;
        width: 46px;
        height: 46px;
    }
}
